<template>
  <div>
    <div class="selectedcontainer">
      <h2>Selected Riders</h2>
      <div id="scatter"></div>
      <!-- <div id="scatterApexcharts"></div> -->
      <div
        class="post"
        v-for="(post, index) in selectedPosts"
        v-bind:item="post"
        v-bind:index="text"
        v-bind:key="post._id"
      >
        <a>
          <button @click="deSelect(post)">-</button>
          {{index}}{{post.name}}
        </a>
      </div>
    </div>
    <h2>Riders</h2>
    <div class="container">
      <div
        class="post"
        v-for="(post) in posts"
        v-bind:item="post"
        v-bind:index="text"
        v-bind:key="post._id"
      >
        <a>
          <button @click="select(post)">+</button>
          {{post.PlacePoints}} {{post.Name}} {{post.Team}} {{post.Time}}
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import StatsService from "../StatsService";

export default {
  name: "PostComponent",
  data() {
    return {
      selectedPosts: [],
      posts: [],
      error: "",
      text: ""
    };
  },
  async created() {
    try {
      this.posts = await StatsService.getPosts();
    } catch (err) {
      this.error = err.message;
    }

    //transformes time to seconds work /w HH:mm:ss:fff
    function timeToSeconds(time) {
      let timeInSecons = 0;
      timeInSecons += parseInt(time[0] * 36000);
      timeInSecons += parseInt(time[1] * 3600);
      timeInSecons += parseInt(time[3] * 600);
      timeInSecons += parseInt(time[4] * 60);
      timeInSecons += parseInt(time[6] * 10);
      timeInSecons += parseInt(time[7]);
      timeInSecons += parseFloat(time[9] / 10);
      timeInSecons += parseFloat(time[10] / 100);
      timeInSecons += parseFloat(time[11] / 1000);

      return timeInSecons;
    }

    // Apexchart
    function createChartSeries(data) {
      var series = [];
      data.forEach(element => {
        if (element.Time !== "") {
          series.push({
            name: `${element.Name}`,
            data: [[timeToSeconds(element.Time), element.PlacePoints]]
          });
        }
      });
      return series;
    }

    var options = {
      chart: {
        height: 350,
        type: "scatter",
        zoom: {
          enabled: true,
          type: "xy"
        }
      },
      series: createChartSeries(this.posts.slice(0, 30)),

      xaxis: {
        tickAmount: 10,
        labels: {
          formatter: function(val) {
            return parseFloat(val).toFixed(1);
          }
        }
      },
      yaxis: {
        tickAmount: 1
      }
    };

    var chart = new ApexCharts(document.getElementById("scatter"), options);
    chart.render();
  },
  methods: {
    select: function(post) {
      this.selectedPosts.push(post);
    },
    deSelect: function(post) {
      function arrayRemove(arr, value) {
        return arr.filter(function(ele) {
          return ele != value;
        });
      }

      this.selectedPosts = arrayRemove(this.selectedPosts, post);
    }
  }
};
</script>

